// Button Styles
.btn {
  @include heading-font;
  font-weight: 700;
}

.btn-xl {
  font-size: 18px;
  padding: 20px 40px;
}
.btn-outline-primary:hover {
  background-color: $gg-1;
  border-color: $gg-1;
}

.btn-outline-primary:active:focus:hover {
  background-color: $gg-1;
  border-color: $gg-1;
}

.btn-outline-primary {
  border-color: $gg-1;
  color: $gg-1;
}
.btn-primary {
  background-color: $primary;
  border-color: $primary;
  &:active,
  &:focus,
  &:hover {
    background-color: darken($primary, 7.5%) !important;
    border-color: darken($primary, 7.5%) !important;
    color: white;
  }
  &:active,
  &:focus {
    box-shadow: 0 0 0 0.2rem rgba(254, 209, 55, 0.5) !important;
  }
}
